#home {
    position:relative;
    background: url('../../assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;

    .app__wraper{
        padding: 0%;

        .copyrigth{
            display: none;
        }
    }
}

.app__header {
    flex:1;
    width: 100%; 
    height: 100%;
    flex-direction: row;
    margin-bottom: 2rem;
    padding: 6rem 2rem 0;
    

    @media screen and (min-width: 2000px) {
        padding: 8rem ;
    }
    @media screen and (max-width: 1200px) {
       flex-direction: column;
    }
    @media screen and (max-width: 450px) {
        padding: 6rem 1rem 2rem;
     }
        
    }
    .app__header-info {
        flex: 0.65;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        height: 100%;

        @media screen and (max-width: 2000px) {
            width :100%;
            margin-right: 0rem;
        }
    }

    .app__header-badge{
       
        width: 100%;
        display: flex;
        justify-content: flex-end;
        flex-direction: column;

        .badge-cmp, .tag-cmp{
         
           padding: 1rem 2rem;
           border: var(--white-color);
           border-radius: 15px;
           flex-direction: row;
           width: auto;

           box-shadow: 0 0 20px rgba(0,0,0,0.2);
        }
        .tag-cmp{
            align-items: center;
            z-index: 2;
            flex-direction: column;
            margin: 3rem;
          

            p { 
                font-weight: 500;
                font-size: 1rem;
                width: 100%;
                text-transform: uppercase;
                text-align: center;
            }
        }
        span {
            font-size: 3rem;

            @media screen and (min-width: 2000px) {
                font-size: 5rem;
            }

        }
        @media screen and (max-width: 1200px) {
            justify-content:flex-start;
            align-items: flex-start;
           
        }
            
        
    }
.app__header-circles{
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;

    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;

        background: var(--white-color);
        box-shadow: 0 0 20px rgba(0,0,0,0.2);

        img {
            width: 60%;
            height: 60%;
           
        }
       
    }
    div:nth-child(1){
        width: 100px;
           height: 100px;
     }
     div:nth-child(2){
         margin: 1.7rem;
         width: 150px;
         height: 150px;
     }
     div:nth-child(3){
         width: 70px;
         height: 70px;
     }
     @media screen and (min-width : 2000px){
        div:nth-child(1){
            width: 400px;
            height: 400px;
        }
        div:nth-child(2){
            margin: 2.5rem;
            width: 300px;
            height: 300px;
        }
        div:nth-child(3){
            width: 200px;
            height: 200px;
        }
     }
     @media screen and (max-width: 1200px) {
       width: 100%;
         margin-left: 0rem;
        flex-direction: row;
        div:nth-child(1){
            width: 100px;
            height: 100px;
        }
        div:nth-child(2){
            margin: 1.7rem;
            width: 150px;
            height: 150px;
        }
        div:nth-child(3){
            width: 70px;
            height: 70px;
        }
     }
        
     }
     .app__header-img {
        flex: 1;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        position: relative;

        img {
            width: 100%;
            z-index: 1;
            object-fit: contain;
        }
        .overlay_circle{
            position: absolute;
           
            z-index: 0;
            right: 0;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 90%;
         }  
            @media screen and (max-width: 1200px) {
              margin: 2rem 0; 
            }
        }       
           
       
  
