.app__works {
    flex:1;
    width: 100%;
    flex-direction: column;
}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 4rem 0 2rem;

    .app__work-filter-item {
        padding: 1rem 1rem;
        border-radius: 0.5rem;
        background-color: #f5f5f5;
        color: #333;
        margin-right: 1rem;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
            background-color: var(--secondary-color);
            color: #fff;
        }
        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            margin-bottom: 1rem;
            border-radius: 0.85rem;
        }
    }
    .item-active {
        background-color: var(--secondary-color);
        color: #fff;
    }
}
.app__work-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item{
        width: 300px;
        flex-direction: column;

        margin: 2rem;
        padding: 1rem;
        border-radius: 0.5rem;
        background-color: #f5f5f5;
        color: #333;
        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &:hover{
            box-shadow: 0 0 25px rgba(0,0,0,0.2);
        }
        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
          
            border-radius: 0.85rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
}

.app__work-img{
    width: 100%;
    height: 100%;

   position: relative;

   img{
         width: 100%;
         height: 100%;
          border-radius: 0.5rem;  
            object-fit: cover;
            object-position: center;
           
        
   }

}
.app__work-hover{
   
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    
 
           
    background-color: rgba(0,0,0,0.5);
    border-radius: 0.5rem;
    opacity: 0; 
    transition: all 0.3s ease-in-out;

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.3);
        color: #fff;

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        svg {
            width: 50%;
            height: 50%;
            color: var (--white-color);
        }
    }
}
.app__work-content{
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4 {
        margin-top: 1.5rem;
        line-height: 1.5rem;
    }

    .app__work-tag {
        position: absolute;
        padding: 0.5rem 1rem ;
        border-radius: 10px;
        background-color: #f5f5f5;
        top: -25px;
    }
}